import React from 'react'
import "./ProfileDesc.css"

//state
//
// 300
export default function ProfileDesc({user}) {
  // console.log(user)
  function checkcity(user) {
    switch(user.city) {
      case "saga": 
        return ""
      case "karatsu":
        return ""
      case "tosu":
        return ""
      case "taku":
        return ""
      case "imari":
        return ""
      case "kashima":
        return ""
      case "ogi":
        return ""
      case "ureshino":
        return ""
      case "kanzaki":
        return ""
    }
  }
  const city = checkcity(user)
  // console.log(city);

  function checkMethod (user) {
    if (user.method) {
      return ""
    } else {
      return ""
    }
  }
  const method = checkMethod(user);

  const teacherdesc = () => {
    return (
      <>
        <div className="type">
          :<br className='hidden' />
          {city}
        </div>
        <div className="type">
          : {user.subject}
        </div>
        <div className="type">
          :<br/>    
          <span>{method}</span> 
        </div>
      </>
    )
  }



  return (
    <div className="profiledesc">
      <div className="descWrapper">
        {teacherdesc()}
      </div>
    </div>
  )
}
