import { convertLength } from '@mui/material/styles/cssUtils';
import axios from 'axios';
import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../../state/AuthContext';
import "./Settings.css"




export default function Settings() {
  const { user } = useContext(AuthContext)

  const navigation = useNavigate();


  const [method, setMethod] = useState(user.method);
  const [username, setuserName] = useState(user.username);
  const [email, setEmail] = useState(user.email);
  const [password, setPassword] = useState(user.password);
  const [city, setCity] = useState(user.city);

  const [subject, setSubject] = useState(user.subject);

  // 
  const [file, setFile] = useState(user.profilePicture);

  // console.log(city);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if(file) {
      //
      const data = new FormData();
      //
      const fileName = Date.now() + file.name;
      data.append("name", fileName);
      data.append("file", file);
      setFile(fileName);

      try {
        //
        await axios.post("/upload", data);
        // setFile(fileName);
      } catch(err) {
        console.log(err);
      }
    }

    try {
      const newUser = {
        _id: user._id,
        profilePicture: file,
        username: username,
        email: email,
        password: password,
        city: city,
        subject: subject,
        method: method,
        type: user.type
      }
      // console.log(newUser);
      if(user.type) {
        await axios.put(`users/teacher/${newUser._id}`, newUser);
        
        localStorage.setItem("user", JSON.stringify(newUser));
      } else {
        await axios.put(`users/student/${user._id}`, newUser);
        // studentUpdate(newUser,dispatch);
        localStorage.setItem("user", JSON.stringify(newUser));
      }
      
      navigation("/home");
    } catch (err) {
      console.log(err);
    }
    
  }



  

  const SettingTeacher = () => {
    return (
      <div className="settings">
        <div className="settingsWrapper">
          <h1 className="settingTop">
            
          </h1>
          <form className="settingBottom" onSubmit={(e) => handleSubmit(e)}>
            <div className="settingItems">
              <div className='settingItem' >
                <p></p>
                <input
                  type="text"
                  className="settingInput"
                  required
                  onChange={(e) => setuserName(e.target.value)}
                  value={username}
                />
              </div>   
              <div className='settingItem' >
                <p></p>
                <input
                  type="email"
                  className="settingInput"
                  required
                  onChange={(e) => setEmail(e.target.value)}
                  value={email}
                />
              </div>
              <div className='settingItem' >
                <p></p>
                <input
                  type="password"
                  className="settingInput" 
                  required
                  minLength="6"
                  onChange={(e) => setPassword(e.target.value)}
                  value={password}
                />
              </div>
              <div className='settingItem' >
                <p></p>
                <select 
                className='settingInput'
                value={city}
                onChange={(e) => setCity(e.target.value)}
                >
                  <option value={"saga"}>
                    
                  </option>
                  <option value={"karatsu"}>
                    
                  </option>
                  <option value={"tosu"}>
                    
                  </option>
                  <option value={"taku"}>
                    
                  </option>
                  <option value={"imari"}>
                    
                  </option>
                  <option value={"takeo"}>
                    
                  </option>
                  <option value={"kashima"}>
                    
                  </option>
                  <option value={"ogi"}>
                    
                  </option>
                  <option value={"ureshino"}>
                    
                  </option>
                  <option value={"kanzaki"}>
                    
                  </option>
                </select>
              </div>
              <div className='settingItem' >
                <p></p>
                <input
                  type="text"
                  className="settingInput"
                  onChange={(e) => setSubject(e.target.value)}
                  value={subject}
                />
              </div>
              {/* <div className="settingItem">
                <p></p>
                <input
                  type="file" 
                  id ="file" 
                  accept=".png, .jpeg, .jpg"
                  
                  onChange={(e) => setFile(e.target.files[0])}
                />
              </div> */}
              <div className='settingItem' >
                <p></p>
                <select
                 className='settingInput'
                 value={method}
                 onChange={(e) => setMethod(e.target.value)}>
                  <option value={false}>
                     
                  </option>
                  <option value={true}>
                    
                  </option>     
                </select>
              </div>
              <button className="loginButton">
                
              </button>
            </div>
          </form>
        </div>
      </div>
    )
  }

  const SettingStudent = () => {
    return (
      <div className="settings">
        <div className="settingsWrapper">
          <h1 className="settingTop">
            
          </h1>
            <form className="settingBottom" onSubmit={(e) => handleSubmit(e)}>
            <div className="settingItems">
              <div className='settingItem' >
                <p></p>
                <input
                  type="text"
                  className="settingInput"
                  onChange={(e) => setuserName(e.target.value)}
                  value={username}
                />
              </div>   
              <div className='settingItem' >
                <p></p>
                <input
                  type="email"
                  className="settingInput"
                  onChange={(e) => setEmail(e.target.value)}
                  value={email}
                />
              </div>
              <div className='settingItem' >
                <p></p>
                <input
                  type="password"
                  className="settingInput" 
                  required
                  minLength="6"  
                  onChange={(e) => setPassword(e.target.value)}
                  value={password}
                />
              </div>
              <div className='settingItem' >
                <p></p>
                <select 
                className='settingInput'
                value={city}
                onChange={(e) => setCity(e.target.value)}
                >
                  <option value={"saga"}>
                    
                  </option>
                  <option value={"karatsu"}>
                    
                  </option>
                  <option value={"tosu"}>
                    
                  </option>
                  <option value={"taku"}>
                    
                  </option>
                  <option value={"imari"}>
                    
                  </option>
                  <option value={"takeo"}>
                    
                  </option>
                  <option value={"kashima"}>
                    
                  </option>
                  <option value={"ogi"}>
                    
                  </option>
                  <option value={"ureshino"}>
                    
                  </option>
                  <option value={"kanzaki"}>
                    
                  </option>
                </select>
              </div>
              <div className='settingItem' >
                <p></p>
                <select value={method}
                 onChange={(e) => setMethod(e.target.value)}className='settingInput'
                >
                  <option value={false}>
                     
                  </option>
                  <option value={true}>
                    
                  </option>
                </select>
              </div>
              <button className="loginButton">
                
              </button>
            </div>
          </form>
        </div>
      </div>
    )
  }

  return (
    <>
      {user.type ? SettingTeacher() : SettingStudent()}
    </>
  )
}
